body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: cornflowerblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#gift {
  width: 300px;
  overflow: visible;
}

.gift {
  position: relative;
}

.gift-lid {
  cursor: pointer;
}

.gift-lid.open {
  -webkit-transform: translate(-137px, 74px) rotate(-42deg);
          transform: translate(-137px, 74px) rotate(-42deg);
}

.confetti-canon {
  position: absolute;
  top: 39%;
  left: 45%;
}
.App {
  text-align: center;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

